import React, { useState, useCallback } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import useSpeechRecognition from "./hooks/useSpeechRecognition";

import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import ProtectedRoute from "./components/ProtectedRoute"; // Path to your ProtectedRoute component
import ChatPage from "./components/ChatPage"; // Import ChatPage
import UserRegistration from "./components/UserRegistration"; // Adjust the path as per your file structure
import UserLogIn from "./components/UserLogin"; // Adjust the path as per your file structure

import Parse from "parse";

function App() {
  const [currentUser] = useState(Parse.User.current());

  // State to store the chat messages
  return (
    <div className="app-container">
      <Router>
        <Routes>
          <Route path="/signup" element={<UserRegistration />} />
          <Route path="/login" element={<UserLogIn />} />
          <Route element={<ProtectedRoute />}>
            {/* Nest protected routes here */}
            <Route path="/chat" element={<ChatPage />} />
            {/* More protected routes */}
          </Route>
          {/* Redirect from root to either chat or login based on authentication */}
          <Route path="/" element={<Navigate to="/chat" replace />} />
          {/* Handle undefined routes - Redirect to login or a specific "NotFound" component */}
          <Route path="*" element={<Navigate to="/login" replace />} />
          {/* other routes */}
        </Routes>
      </Router>
    </div>
  );
}

export default App;
