import React, { createContext, useContext, useState, useEffect } from 'react';

import Parse from "parse";

const AuthContext = createContext();

export const useAuth = () => useContext(AuthContext);

export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(Parse.User.current());

  useEffect(() => {
    // Here, integrate with Parse or your authentication system to check user session
    Parse.User.currentAsync().then(user => {
      console.log("Detected user on load:", user); // Debugging line
      setCurrentUser(user);
    });
  }, []);

  const updateUserContext = (user) => {
    setCurrentUser(user);
  };

  return (
    <AuthContext.Provider value={{ currentUser, updateUserContext }}>
      {children}
    </AuthContext.Provider>
  );
};

export default AuthProvider;
