import React, { useEffect, useRef } from 'react';
import DOMPurify from 'dompurify';
import LandingPage from './LandingPage';
import TableComponent from './TableComponent';
import VideoComponent from './VideoComponent';
import LinkComponent from './LinkComponent';
import TextComponent from './TextComponent';
import './ChatWindow.css';
// ... other imports

function ChatWindow({ messages, isWaiting, currentUser, conversationStarters, onStarterSelect}) {
  // At the beginning of the ChatWindow function
  console.log("ChatWindow props - currentUser:", currentUser, "messages:", messages);

  const endOfMessagesRef = useRef(null);

  useEffect(() => {
    endOfMessagesRef.current?.scrollIntoView({ behavior: 'auto' });
  }, [messages, isWaiting, currentUser]);


  const renderMessage = (message) => {
    // Determine message type (assuming 'currentUser' is defined in your context)
    const messageType = message.sender === currentUser.id ? 'message-outgoing' : 'message-incoming';
  
    if (typeof message.content === 'object' && message.content !== null) {
      // Render complex message with the determined message type
      return <div className={`${messageType}`}>{renderComplexMessage(message.content)}</div>;
    } else {
      // Render simple text message with the determined message type
      return <div className={`${messageType}`}><p>{message.content}</p></div>;
    }
  };
  

  const HtmlContentComponent = ({ html }) => (
    <div dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(html) }} />
  );

  const renderComplexMessage = (content) => {
    if (content.text) {
      const htmlText = convertMarkdownToHtml(content.text);
      return <HtmlContentComponent html={htmlText} />;
    }
    // Assume content.text can include markdown-like syntax for images and links
    if (content.text) {
      const sanitizedText = DOMPurify.sanitize(content.text);
      // You might want to replace this with actual markdown parsing
      // For now, let's assume you convert markdown links and images to HTML here
      const htmlText = convertMarkdownToHtml(sanitizedText);
      return <TextComponent htmlText={htmlText} />;
    }
    // Existing handlers for image, link, video, etc.
    if (content.image) {
      return <img src={content.image} alt={content.alt || 'Image'} />;
    }
    if (content.link) {
      return <LinkComponent href={content.link} />;
    }
    if (content.video) {
      return <VideoComponent src={content.video} />;
    }
    if (content.table) {
      return <TableComponent data={content.table} />;
    }
    // Add more conditions as needed
  };
  
  const convertMarkdownToHtml = (text) => {
    // Split the text into lines for processing
    const lines = text.split('\n');
    let inList = false; // Track if we're currently processing a list
    let html = ''; // Accumulate the HTML output
  
    lines.forEach(line => {
      if (line.trim().startsWith('- ')) { // Unordered list item
        if (!inList) {
          html += '<ul>'; // Start a new list
          inList = true;
        }
        // Add the list item, removing the '- ' marker
        html += `<li>${line.trim().substring(2)}</li>`;
      } else {
        if (inList) {
          html += '</ul>'; // Close the current list
          inList = false;
        }
        // For simplicity, just add non-list lines as paragraphs
        // A more comprehensive approach would handle other patterns
        html += `<p>${line}</p>`;
      }
    });
  
    if (inList) {
      html += '</ul>'; // Ensure any open list is closed
    }
  
    return html;
  };

  if (currentUser) {
    return (
      <div className="chat-messages-container">
      <div>Hi {currentUser.getUsername()}!</div>

      {/* <UserLogIn /> */}
      <LandingPage />
      <div className="conversation-starters">
        {conversationStarters.map((starter, index) => (
          <button
            key={index}
            className="conversation-starter-button"
            onClick={() => onStarterSelect(starter)}
          >
            {starter}
          </button>
        ))}
    </div>
        {messages.map((message, index) => (
        <div key={index} className="message">
          {renderMessage(message)}
        </div>
      ))}
        <div ref={endOfMessagesRef}></div>
        {isWaiting && <ThinkingAnimation />}
      </div>
    );
  }

  return (
    <div className="chat-messages-container">
      <div>Hi, {currentUser.getUsername()}! </div>
      <div>Please contact Admin, if you are seeing this message.</div>

      {messages.map((message) => (
        <div key={message.id} className="message">
          {renderMessage(message)}
        </div>
      ))}
      <div ref={endOfMessagesRef}></div>
      {isWaiting && <ThinkingAnimation />}
    </div>
  );
}

// ... other components like ThinkingAnimation
// Thinking animation component
const ThinkingAnimation = () => (
  <div className="thinking-animation">
    <div className="dot-typing"></div>
  </div>
);

export default ChatWindow;
