// ChatPage.js
import React, { useState, useCallback, useContext } from 'react';
import ChatWindow from './ChatWindow';
import InputArea from './InputArea';
import { useAuth } from './AuthContext'; // Adjust import path as necessary
import useSpeechRecognition from "../hooks/useSpeechRecognition";
import axios from "axios"; // Import Axios

const ChatPage = () => {

  const conversationStarters = [
    "Add a new inventory item",
    "Create a new sales order",
    "Get total sales for this month",
    "Get inventory levels",
    
  ];

  const handleStarterSelect = (starterText) => {
    handleSendMessage(starterText, isTtsEnabled);
  };
  const { currentUser } = useAuth(); // Accessing current user
  const [messages, setMessages] = useState([]); // Messages state
  const [isWaiting, setIsWaiting] = useState(false); // Loading/waiting state

  
  // State to store the chat messages

  const [isTtsEnabled, setIsTtsEnabled] = useState(false);
  const { transcript, startListening, setTranscript } = useSpeechRecognition();

  const clearTranscript = useCallback(() => {
    setTranscript(""); // Resetting the transcript
  }, [setTranscript]);

  const [audioUrl, setAudioUrl] = useState(null);

  const speakOutLoud = async (text) => {
    if (isTtsEnabled) {
      try {
        const response = await axios.post(
          "https://odesys.pythonanywhere.com/tts",
          { text },
          {
            responseType: "blob", // Important for handling binary data
          }
        );

        const audioBlob = new Blob([response.data], { type: "audio/mp3" });
        const audioUrl = URL.createObjectURL(audioBlob);
        setAudioUrl(audioUrl);

        // Additional logic for handling hands-free mode
        // ...
      } catch (error) {
        console.error("Error in text-to-speech:", error);
        // Handle the error appropriately in your app
      }
    }
  };
  const [isHandsFreeMode, setIsHandsFreeMode] = useState(false);

  
  const handleHandsFreeChange = (isEnabled) => {
    setIsHandsFreeMode(isEnabled);
    // Additional logic if needed when hands-free mode changes
  };

  const handleTtsChange = (isEnabled) => {
    setIsTtsEnabled(isEnabled);
  };

  const handleSendMessage = async (newMessageContent, isTtsEnabled) => {
    setIsWaiting(true); // Disable input and show spinner
    const newMessage = {
      id: messages.length + 1,
      content: newMessageContent,
      sender: currentUser.id,
    };

    // Update the messages state with the new message
    setMessages((prevMessages) => [...prevMessages, newMessage]);

    try {
      // Prepare the payload
      const currentDate = new Date().toLocaleDateString();
      const payload = {
        question: newMessageContent,
        chatId: currentUser.id + "-"+currentDate, // Replace with an actual unique ID if needed
      };

      // API call with Axios
      const response = await axios.post(
        "https://parseapi.back4app.com/functions/processRequest",
        payload,
        {
          headers: {
            "Content-Type": "application/json",
            "X-Parse-Application-Id":
              "JTMfkpX9VCDKlXlMRZkmjBLXfvkIYad8q85kMR22",
            "X-Parse-Client-Key": "FncsXHYxWvERMuUQtJcKlEUMoNYghqvkRjPvn3wW",
          },
        }
      );

      // Assuming the response contains the message in a similar format
      const assistantMessage = {
        id: messages.length + 2,
        content: response.data.result,
        sender: "Assistant",
      };

      setMessages((prevMessages) => [...prevMessages, assistantMessage]);
      if (assistantMessage.content.text && isTtsEnabled) {
        speakOutLoud(assistantMessage.content.text);
      } else if (isHandsFreeMode) {
        startListening();
      }
    } catch (error) {
      console.error("Error sending message:", error);
      // Handle the error appropriately in your app
    }

    setIsWaiting(false); // Re-enable input and hide spinner
  };

  return (
    <>
    
    <div>
      {/* Existing chat window UI */}
      
    </div>
      <ChatWindow 
        conversationStarters={conversationStarters}
        onStarterSelect={handleStarterSelect}
        currentUser={currentUser}
        messages={messages}
        isWaiting={isWaiting} />
      
      {/* Audio Player */}
      {audioUrl && <audio src={audioUrl} controls autoPlay />}
      <InputArea 
        onSendMessage={handleSendMessage}
        isDisabled={isWaiting}
        onTtsChange={handleTtsChange} // Passing the callback
        isHandsFreeMode={isHandsFreeMode}
        onHandsFreeChange={handleHandsFreeChange}
        transcript={transcript}
        clearTranscript={clearTranscript} />
    </>
  );
};

export default ChatPage;
