import React, { useState, useEffect } from 'react';
import './DashboardSection.css';

const DashboardSection = () => {
  const [inventoryTurnover, setInventoryTurnover] = useState(0);
  const [sellThroughRate, setSellThroughRate] = useState(0);
  const [gmroi, setGmroi] = useState(0);

  useEffect(() => {
    // The fetchMetrics function should be replaced with your actual data fetching logic
    const fetchMetrics = async () => {
      // Fetch data for inventoryTurnover, sellThroughRate, and gmroi
      try {
        const inventoryTurnoverResponse = await fetch('/path/to/inventoryTurnoverFunction', { /* request setup */ });
        const sellThroughRateResponse = await fetch('/path/to/sellThroughRateFunction', { /* request setup */ });
        const gmroiResponse = await fetch('/path/to/gmroiFunction', { /* request setup */ });
  
        // Assuming the response body directly contains the metric value
        const inventoryTurnoverData = await inventoryTurnoverResponse.json();
        const sellThroughRateData = await sellThroughRateResponse.json();
        const gmroiData = await gmroiResponse.json();
  
        setInventoryTurnover(inventoryTurnoverData.value);
        setSellThroughRate(sellThroughRateData.value);
        setGmroi(gmroiData.value);
      } catch (error) {
        console.error('Failed to fetch metrics:', error);
      }
    };

    fetchMetrics();
  }, []);

  return (
    <div className="dashboard-section">
      <div className="dashboard-metrics">
        <div className="metric">
          <h3>Inventory Turnover Ratio</h3>
          <p className="metric-description">Stock Sales Speed</p>
          <p>{inventoryTurnover.toFixed(2)}</p>
        </div>
        <div className="metric">
          <h3>Sell-Through Rate</h3>
          <p className="metric-description">Sold Stock Percentage</p>
          <p>{(sellThroughRate * 100).toFixed(2)}%</p>
        </div>
        <div className="metric">
          <h3>GMROI</h3>
          <p className="metric-description">Earnings From Stock</p>
          <p>{gmroi.toFixed(2)}</p>
        </div>
      </div>
    </div>
  );
};

export default DashboardSection;
