import React, { useState } from 'react';
import Parse from 'parse';

export const UserRegistration = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const doUserRegistration = async () => {
    try {
      const createdUser = await Parse.User.signUp(username, password);
      console.log(`User ${createdUser.getUsername()} was successfully created!`);
      // Navigate to login or main content
    } catch (error) {
      console.error(`Error: ${error.message}`);
    }
  };

  return (
    <div>
      <input type="text" value={username} onChange={(e) => setUsername(e.target.value)} />
      <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
      <button onClick={doUserRegistration}>Sign Up</button>
    </div>
  );
};


export default UserRegistration;
