import React, { useState,useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Parse from 'parse';
import './UserLogin.css';
import { useAuth } from './AuthContext'; // Path to your AuthContext file


const UserLogIn = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const { updateUserContext } = useAuth();

  const navigate = useNavigate();

  
const { currentUser } = useAuth(); // Import and use the Auth context

const doUserLogin = async (e) => {
  e.preventDefault();
  setIsLoading(true);
  setError("");
  try {
    const user = await Parse.User.logIn(username, password);
    console.log(`User ${user.getUsername()} is logged in!`);
    updateUserContext(user);
    navigate('/');
    // No immediate navigate call here
  } catch (error) {
    console.error(`Error: ${error.message}`);
    setError(error.message);
  } finally {
    setIsLoading(false);
  }
};

return (
    <div>
    <h2>T&uuml;elv</h2>
    <div className="login-container">
      <form className="login-form" onSubmit={doUserLogin}>
        <input
          type="text"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          placeholder="Username"
          autoComplete="username" // Suggested attribute for username
          disabled={isLoading}
          required
        />
        <input
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="Password"
          autoComplete="current-password" // Suggested attribute for current password
          disabled={isLoading}
          required
        />
        <button type="submit" disabled={isLoading}>
          Log In
        </button>
        {error && <p style={{ color: 'red' }}>{error}</p>}
      </form>
    </div>
    </div>
  );
};

export default UserLogIn;
