import React, { useState, useEffect } from 'react';
import './InputArea.css'; // Make sure to create this CSS file with the styles provided below
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

import '@fortawesome/fontawesome-free/css/all.min.css';

// Import Web Speech API
const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;


function InputArea({ onSendMessage, isDisabled, onTtsChange, onHandsFreeChange, transcript, clearTranscript, selectedStarter }) {
    const [isListening, setIsListening] = useState(false);

    const [inputValue, setInputValue] = useState('');
    const [isHandsFreeMode, setIsHandsFreeMode] = useState(false);


    const handleInputChange = (e) => {
        setInputValue(e.target.value);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if (typeof onSendMessage === 'function' && inputValue.trim()) {
          onSendMessage(inputValue.trim(), isTtsEnabled);
          setInputValue('');
        } else {
          console.error('onSendMessage is not a function');
        }
      };
      

      
    // Add a state to track if TTS is enabled
    const [isTtsEnabled, setIsTtsEnabled] = useState(false);
      // Example checkbox items
    const [dropdownCheckboxes, setDropdownCheckboxes] = useState([
        { id: 1, label: "Speaker Mode", checked: false },
        { id: 2, label: "Hands Free", checked: false },
    ]);


    const handleCheckboxChange = (id) => {
      setDropdownCheckboxes(dropdownCheckboxes.map(item => {
        if (item.id === id) {
          const isChecked = !item.checked;
          console.log(`Checkbox ${item.label} is now ${isChecked ? 'checked' : 'unchecked'}`);
    
          if (item.label === "Speaker Mode") {
            setIsTtsEnabled(isChecked);  // Assuming this state is still managed here
            onTtsChange(isChecked);      // Callback to parent
          } 
          if (item.label === "Hands Free") {
            setIsHandsFreeMode(isChecked);
            onHandsFreeChange(isChecked); // Use callback to update in parent
          }
          return { ...item, checked: isChecked };
        }
        return item;
      }));
    };
    
    const beep = (duration, frequency, volume) => {
      const audioContext = new (window.AudioContext || window.webkitAudioContext)();
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
    
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);
    
      gainNode.gain.value = volume * 0.01;
      oscillator.frequency.value = frequency;
      oscillator.type = 'square';
    
      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + duration * 0.001);
    };
    

    const startSpeechRecognition = () => {
      if (!SpeechRecognition) {
        console.error("Speech recognition not supported in this browser.");
        return;
      }
  
      const recognition = new SpeechRecognition();
      recognition.lang = 'en-NG';
      recognition.interimResults = false;
  
      recognition.onstart = () => {
        beep(200, 500, 100); // Higher frequency beep for start
        console.log("Speech recognition started");
        setIsListening(true); // Set listening state to true
      };
  
      recognition.onresult = (event) => {
        const transcript = event.results[0][0].transcript;
        // setInputValue(transcript); // Set the transcribed text to input value
        // Automatically send the message
        onSendMessage(transcript, isTtsEnabled);
      };

      recognition.onend = () => {
        console.log("Speech recognition ended");
        setIsListening(false); // Set listening state to false
        beep(150, 440, 100); // Lower frequency beep for end
      };
  
      recognition.start();
    };


    // Effect to handle spoken messages
    useEffect(() => {
      console.log(`Type of onSendMessage: ${typeof onSendMessage}`); // Check the type
      if (selectedStarter) {
        console.log("Convo starter for InputArea:", selectedStarter);
        // Optionally, if you want to send the message immediately
        onSendMessage(selectedStarter, isTtsEnabled);
      }
      else if (transcript && transcript !== '') {
        console.log("Transcript received in InputArea:", transcript);
        onSendMessage(transcript, isTtsEnabled);
        // Reset the transcript in parent component or via callback if necessary
        clearTranscript(); // Call this function after sending the message
      
      }
    }, [selectedStarter, transcript, onSendMessage, isTtsEnabled, clearTranscript]);

      return (
        <div className="input-area-container">
          <form className="input-form" onSubmit={handleSubmit}>
                {/* Dropdown menu */}
                <div className="btn-group">
                    <button 
                        className="btn icon-button dropdown-toggle" 
                        type="button" 
                        id="dropdownMenuButton" 
                        data-bs-toggle="dropdown" 
                        aria-expanded="false"
                        disabled={isDisabled}
                    >
                        <i className="fas fa-ellipsis-v"></i>
                    </button>
                    <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        {dropdownCheckboxes.map((item) => (
                            <li key={item.id}>
                                <div className="dropdown-item">
                                    <input 
                                        type="checkbox" 
                                        id={`dropdown-checkbox-${item.id}`}
                                        checked={item.checked}
                                        onChange={() => handleCheckboxChange(item.id)}
                                    />
                                    <label htmlFor={`dropdown-checkbox-${item.id}`}>{item.label}</label>
                                </div>
                            </li>
                        ))}
                    </ul>
                </div>
            <button className="btn icon-button" type="button" disabled={isDisabled}>
              <i className="fas fa-paperclip"></i>
            </button>
            <div className="form-group flex-grow">
              <input
                id="question"
                type="text"
                className="form-control message-input"
                placeholder="Type your message here..."
                aria-label="Type your message here" // ARIA label for screen readers
                value={inputValue}
                onChange={handleInputChange}
                disabled={isDisabled}
              />
              <span className="visually-hidden" aria-live="polite">
                {isListening ? "Listening..." : "Not listening"}
                </span>
            </div>
            <button className="btn icon-button" type="submit" disabled={isDisabled}>
              {isDisabled ? (
                <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
              ) : (
                <i className="fas fa-paper-plane"></i>
              )}
            </button>
            <button className={`btn icon-button ${isListening ? 'listening' : ''}`} type="button" onClick={startSpeechRecognition} disabled={isDisabled} aria-pressed={isListening}>
              <i className="fas fa-microphone"></i>
            </button>
          </form>
        </div>
      );
}

export default InputArea;
