import { useState, useEffect, useRef, useCallback } from "react";

const useSpeechRecognition = () => {
  const SpeechRecognition =
    window.SpeechRecognition || window.webkitSpeechRecognition;
  const recognitionRef = useRef(null);
  const [isListening, setIsListening] = useState(false);
  const [transcript, setTranscript] = useState("");
  const [error, setError] = useState("");

  const startListening = useCallback(() => {
    if (!SpeechRecognition) {
      console.error("Speech recognition not supported in this browser.");
      setError("Speech recognition not supported in this browser.");
      return;
    }

    recognitionRef.current = new SpeechRecognition();
    recognitionRef.current.lang = "en-NG";
    recognitionRef.current.interimResults = false;

    recognitionRef.current.onstart = () => {
      console.log("Speech recognition started");
      setIsListening(true);
    };

    recognitionRef.current.onresult = (event) => {
      const newTranscript = event.results[0][0].transcript;
      console.log("New transcript from recognition:", newTranscript); // Log for debugging
      setTranscript((prevTranscript) => prevTranscript + newTranscript);
    };

    recognitionRef.current.onend = () => {
      console.log("Speech recognition ended");
      setIsListening(false);
    };

    recognitionRef.current.start();
  }, [SpeechRecognition]);

  const stopListening = useCallback(() => {
    if (recognitionRef.current) {
      recognitionRef.current.stop();
    }
  }, []);

  useEffect(() => {
    // Cleanup function to stop recognition when the component unmounts
    return () => {
      if (recognitionRef.current) {
        recognitionRef.current.stop();
      }
    };
  }, []);

  return {
    isListening,
    transcript,
    setTranscript,
    error,
    startListening,
    stopListening,
  };
};

export default useSpeechRecognition;
